// å¯¼å‡ºä¸­æ–‡æ”¯æŒçš„è¾…åŠ©å‡½æ•°
export function setupChineseSupport(pdf: any) {
  try {
    // è®¾ç½®é»˜è®¤å­—ä½“
    pdf.setFont('helvetica');

    // ä¼˜åŒ–ä¸­æ–‡å¤„ç†
    const encodingFallback = function(text: string) {
      try {
        // ç®€å•æ›¿æ¢ä¸€äº›ä¸­æ–‡æ ‡ç‚¹ç¬¦å·ä¸ºè‹±æ–‡æ ‡ç‚¹
        return text
          .replace(/ï¼š/g, ':')
          .replace(/ï¼Œ/g, ',')
          .replace(/ã€‚/g, '.')
          .replace(/ï¼ˆ/g, '(')
          .replace(/ï¼‰/g, ')')
          .replace(/ï¼›/g, ';');
      } catch (e) {
        return text;
      }
    };

    // ä¿å­˜åŸå§‹çš„textæ–¹æ³•
    const originalText = pdf.text;

    // è¦†ç›–textæ–¹æ³•ï¼Œæ·»åŠ ä¸­æ–‡å¤„ç†
    pdf.text = function(text: string, x: number, y: number, options?: any) {
      try {
        // å°è¯•ä½¿ç”¨åŸå§‹æ–¹æ³•
        return originalText.call(this, encodingFallback(text), x, y, options);
      } catch (e) {
        console.warn('æ–‡å­—æ¸²æŸ“å¤±è´¥:', e);
        // å¦‚æœå¤±è´¥ï¼Œå°è¯•ç›´æ¥æ¸²æŸ“ASCIIå­—ç¬¦
        const asciiOnly = text.replace(/[^\x00-\x7F]/g, '?');
        return originalText.call(this, asciiOnly, x, y, options);
      }
    };

    return pdf;
  } catch (e) {
    console.warn('ä¸­æ–‡æ”¯æŒè®¾ç½®å¤±è´¥:', e);
    return pdf;
  }
}
